.button-wrapper
{
    height: 4em;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: lighter;
    font-size: 1.4em;
    background-color: #E0E1E6;
    color: #888;
    flex: 8;
    outline: 1px solid #888;
}

.button-wrapper:hover
{
    cursor: pointer;
}

.operator
{
    color: white;
    background-color: #FE9241;
}