.button-wrapper
{
    height: 4em;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    font-weight: lighter;
    font-size: 1.4em;
    background-color: #E0E1E6;
    color: #888;
    -webkit-flex: 8 1;
            flex: 8 1;
    outline: 1px solid #888;
}

.button-wrapper:hover
{
    cursor: pointer;
}

